import React from "react";
import moment from "moment";

class VideoListItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: false
    };
  }

  render() {
    let imgUrl = this.props.video.snippet.thumbnails.medium.url;
    let {
      title,
      channelTitle,
      description,
      publishedAt
    } = this.props.video.snippet;

    let thumbnailContent;

    if (this.props.selectedVideo) {
      thumbnailContent = (
        <div>
          <p>{channelTitle}</p>
          <span>{moment(publishedAt).fromNow()}</span>
        </div>
      );
    } else {
      thumbnailContent = (
        <div>
          <span>{channelTitle}</span>
          <span className="px-2">&bull;</span>
          <span>{moment(publishedAt).fromNow()}</span> <br></br>
          <br></br>
          <p>{description}</p>
        </div>
      );
    }

    return (
      <li
        className="list-group-item list-group-item-action cur-pointer"
        onClick={() => this.props.handleVideoSelect(this.props.video)}
      >
        <div className="media row">
          <img src={imgUrl} alt={title} className="col col-sm-4 pb-3" />
          <div className="media-body pl-3 col col-sm-8">
            <p className="title font-weight-bold">{title}</p>
            {thumbnailContent}
          </div>
        </div>
      </li>
    );
  }
}

export default VideoListItem;
