import React from "react";
import videos from "./videos";
// import SearchBar from "./components/SearchBar";
import NavBar from "./components/NavBar";
import VideoList from "./components/VideoList";
import VideoDetail from "./components/VideoDetail";

// import "./styles/App.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      videos,
      selectedVideo: null
    };
  }

  handleVideoSelect = video => {
    this.setState({ selectedVideo: video });

    setTimeout(() => {
      let iframe = document.querySelector("iframe");

      iframe.scrollIntoView(false);
    }, 200);
  };

  handleSearch = searchTerm => {
    fetch(`https://api-skhatri-xyz.herokuapp.com/yt/${searchTerm.trim()}`)
      .then(response => response.json())
      .then(data => {
        this.setState({ videos: data.items, selectedVideo: null });
      });
  };

  componentDidMount() {
    // Just to wake up heroku server, if it is asleep so that search can be done quickly.
    fetch("https://api-skhatri-xyz.herokuapp.com/email/test").then(response => {
      setInterval(() => {
        fetch(
          "https://api-skhatri-xyz.herokuapp.com/email/test"
        ).then(response => {});
      }, 840000);
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <NavBar handleSearch={this.handleSearch} />
        <div
          className={`${this.state.selectedVideo ? "media row" : "container"}`}
        >
          <VideoDetail
            video={this.state.selectedVideo}
            selectedVideo={this.state.selectedVideo}
          />
          <VideoList
            videos={this.state.videos}
            handleVideoSelect={this.handleVideoSelect}
            selectedVideo={this.state.selectedVideo}
          />
        </div>
      </div>
    );
  }
}

export default App;
