import React from "react";
import moment from "moment";

function VideoDeatil(props) {
  if (props.video) {
    let srcUrl = `https://www.youtube.com/embed/${props.video.id.videoId}`;
    let {
      title,
      publishedAt,
      description,
      channelId,
      channelTitle
    } = props.video.snippet;

    return (
      <div className={`${props.selectedVideo ? "col col-12 col-lg-8" : ""}`}>
        <div
          className={`embed-responsive embed-responsive-16by9 mx-auto`}
          style={{ maxWidth: 900 + "px", maxHeight: 500 + "px" }}
        >
          <iframe
            className="embed-responsive-item"
            title={srcUrl}
            width="1020"
            height="538"
            src={srcUrl}
            frameBorder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
        <div className="pt-3">
          <p className="title font-weight-bold">{title}</p>
          <p>{moment(publishedAt).format("lll")}</p> <hr></hr>
          <div className="media">
            <a
              href={`https://www.youtube.com/channel/${channelId}`}
              rel="noopener noreferrer"
              target="_blank"
              className="align-self-start mr-3"
            >
              <i
                style={{ color: "red", fontSize: 4 + "em" }}
                className="fas fa-user-circle pr-2 "
              ></i>
            </a>
            <div className="media-body">
              <p className="font-weight-bold">{channelTitle}</p>
              <p>{description}</p>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return <div></div>;
  }
}

export default VideoDeatil;
